html, body {
  height: 100%; 
}

body {  
  color: $darktext;
  background-color: $white; 
}

a {
  color: $black;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out; 
}

a:hover {
  color: $black; 
}

a, a:hover, a:active, a:focus, button:focus {
  outline: 0;
  border: 0;
  text-decoration: none; 
}

#st-preloader {
  background: $white;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 9999; 
}

input, textarea {
  outline: none; 
}


#pre-status,
.preload-placeholder {
  background-image: url(./images/preloader.gif);
  background-position: center;
  background-repeat: no-repeat;
  height: 40px;
  left: 50%;
  margin: -20px 0 0 -20px;
  position: absolute;
  top: 50%;
  width: 40px; 
}

.preload-placeholder {
  background: none;
  left: 0;
  margin: 0;
  text-align: center;
  top: 65%; 
}

.st-border {
  width: 34px;
  height: 1px;
  display: block;
  background-color: $black; 
}

.overlay {
  background-color: $black;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.8; 
}